import { Component, OnInit } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { NzMessageService } from 'ng-zorro-antd';
import { NzFormatEmitEvent } from 'ng-zorro-antd';
import { CookieService } from 'ngx-cookie-service';
import { MySharedService } from '../../my-shared.service';

@Component({
  selector: 'app-sys-user',
  templateUrl: './sys-user.component.html',
  styleUrls: ['./sys-user.component.css']
})
export class SysUserComponent implements OnInit {
  constructor(
    private http: HttpClient,
    private message: NzMessageService,
    private cookieService: CookieService,
    private mySharedService: MySharedService
  ) {

  }
  showwait = false;
  AuthList:any;
  SysUserList: any;
  TotalCount: any;
  myDate: any;
  limitDate: any;
  option = {
    Page: 1,
    Number: 10,
    SearchContent: "",
    AppletId: 0,
  };
  isAddVisible = false;
  isModifyVisible = false;
  isOkLoading = false;
  RoleList: any;
  AddSysUserObj = {
    Account: "",
    Name: "",
    Phone: "",
    RoleId: 0,
    RoleName: "",
  };
  ModifyObj = {
    RoleName: "",
  };
  AppletId = 0;
  ngOnInit() {
    this.AuthList = this.mySharedService.getGlobalVar();
    this.GetSysUserList();
  }
  onIndexChange(e) {
    this.option.Page = e;
    this.GetSysUserList();
  }
  AppletList:any;
  //获取所有角色
  GetRoleList() {   
    let url = "api/Sys_User/GetRoles";
    this.http.get(url).subscribe(
      (data) => {
        this.RoleList = data;
      },
      response => {

      });
  }
  //获取系统用户列表
  GetSysUserList() {
    this.showwait = true;
    this.option.AppletId = this.AppletId;
    let url = "api/Sys_User/GetSysUser";
    this.http.post(url, this.option).subscribe(
      (data) => {
        this.myDate = data;
        console.log(data);
        this.SysUserList = this.myDate.bigField;
        this.TotalCount = this.myDate.totalCount;
        this.showwait = false;
      },
      response => {
        this.showwait = false;
      });
  }
  //新增模态框
  showAddModal(): void {
    this.isAddVisible = true;
    this.AddSysUserObj = {
      Account: "",
      Name: "",
      Phone: "",      
      RoleId: 0,
      RoleName: "",
    };
    this.GetRoleList();
  }
  handleCancel(): void {
    this.isAddVisible = false;
  }
  //新增系统用户
  AddSysUser() {
    this.isOkLoading = true;
    let url = "api/Sys_User/AddSysUser";
    this.http.post(url, this.AddSysUserObj).subscribe(
      (val) => {
        this.GetSysUserList();
        this.isAddVisible = false;
        this.isOkLoading = false;
        this.message.create("success", val.toString(), { nzDuration: 3000 });     
      },
      response => {
        this.isAddVisible = false;
        this.isOkLoading = false;
        this.message.create("error", response, { nzDuration: 3000 });
      });
  }
  //编辑模态框
  showModifyModal(obj) {
    this.isModifyVisible = true;
    this.ModifyObj = obj;
    console.log(this.ModifyObj);
  }
  handleModifyCancel(): void {
    this.isModifyVisible = false;
  }
  //修改系统用户
  ModifySysUser() {
    console.log(this.ModifyObj);
    this.isOkLoading = true;
    let url = "api/Sys_User/ModifySysUser";
    this.http.post(url, this.ModifyObj).subscribe(
      (val) => {
        this.GetSysUserList();
        this.isModifyVisible = false;
        this.isOkLoading = false;
        this.message.create("success", val.toString(), { nzDuration: 3000 });
      },
      response => {
        this.isModifyVisible = false;
        this.isOkLoading = false;
        this.message.create("error", response, { nzDuration: 3000 });
      });
  }
  //删除系统用户
  delsysuser(id) {
    let url = "api/Sys_User/DeleteSysUser?id=" + id;
    this.http.delete(url).subscribe(
      (val) => {
        this.GetSysUserList();
        this.message.create("success", val.toString(), { nzDuration: 3000 });
      },
      response => {
        this.message.create("error", response, { nzDuration: 3000 });
      });
  }
  //初始化系统用户密码
  initpwd(id) {
    let url = "api/Sys_User/InitPwd?id=" + id;
    this.http.get(url).subscribe(
      (val) => {
        this.GetSysUserList();
        this.message.create("success", val.toString(), { nzDuration: 3000 });
      },
      response => {
        this.message.create("error", response, { nzDuration: 3000 });
      });
  }
  selectrole_add(e){
    this.AddSysUserObj.RoleName = e.name;
  }
  selectrole_edit(e){
    this.ModifyObj.RoleName = e.name;
  }

}
