import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { NzMessageService,UploadFile } from 'ng-zorro-antd';
import { CookieService } from 'ngx-cookie-service';
import { MySharedService } from '../../my-shared.service';
@Component({
  selector: 'app-service',
  templateUrl: './service.component.html',
  styleUrls: ['./service.component.css']
})
export class ServiceComponent implements OnInit {

  constructor(
    private http: HttpClient,
    private message: NzMessageService,
    private cookieService: CookieService,
    private mySharedService: MySharedService
  ) {}

  showwait = false;
  AuthList:any;
  ServiceList: any;
  myDate: any;
  limitDate: any;
  isAddVisible = false;
  isModifyVisible = false;
  isOkLoading = false;
  AddServiceObj = {
    Name: "",
  };
  ModifyObj = {};
  AppletId = 0;
  ngOnInit() {
    this.AuthList = this.mySharedService.getGlobalVar();
    this.GetServiceList();
  }
  AppletList:any;
  //获取
  GetServiceList() {
    this.showwait = true;
    let url = "api/Service/GetServices";
    this.http.get(url).subscribe(
      (data) => {
        this.myDate = data;
        console.log(data);
        this.ServiceList = this.myDate;
        this.showwait = false;
      },
      response => {
        this.showwait = false;
      });
  }
  //新增模态框
  showAddModal(): void {
    this.isAddVisible = true;
    this.AddServiceObj = {
      Name: "",
    };
  }
  handleCancel(): void {
    this.isAddVisible = false;
  }
  //新增
  AddService() {
    this.isOkLoading = true;
    let url = "api/Service/Create";
    this.http.post(url, this.AddServiceObj).subscribe(
      (val) => {
        this.GetServiceList();
        this.isAddVisible = false;
        this.isOkLoading = false;
        this.message.create("success", val.toString(), { nzDuration: 3000 });     
      },
      response => {
        this.isAddVisible = false;
        this.isOkLoading = false;
        this.message.create("error", response, { nzDuration: 3000 });
      });
  }
  //编辑模态框
  showModifyModal(obj) {
    this.isModifyVisible = true;
    this.ModifyObj = obj;
    console.log(this.ModifyObj);
  }
  handleModifyCancel(): void {
    this.isModifyVisible = false;
  }
  //修改
  ModifyService() {
    console.log(this.ModifyObj);
    this.isOkLoading = true;
    let url = "api/Service/Modify";
    this.http.post(url, this.ModifyObj).subscribe(
      (val) => {
        this.GetServiceList();
        this.isModifyVisible = false;
        this.isOkLoading = false;
        this.message.create("success", val.toString(), { nzDuration: 3000 });
      },
      response => {
        this.isModifyVisible = false;
        this.isOkLoading = false;
        this.message.create("error", response, { nzDuration: 3000 });
      });
  }
  //删除
  del(id) {
    let url = "api/Service/Delete?Id=" + id;
    this.http.delete(url).subscribe(
      (val) => {
        this.GetServiceList();
        this.message.create("success", val.toString(), { nzDuration: 3000 });
      },
      response => {
        this.message.create("error", response, { nzDuration: 3000 });
      });
  }
}
