import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { NzMessageService,UploadFile } from 'ng-zorro-antd';
import { CookieService } from 'ngx-cookie-service';
import { MySharedService } from '../../my-shared.service';
@Component({
  selector: 'app-user',
  templateUrl: './user.component.html',
  styleUrls: ['./user.component.css']
})
export class UserComponent implements OnInit {

  constructor(
    private http: HttpClient,
    private message: NzMessageService,
    private cookieService: CookieService,
    private mySharedService: MySharedService
  ) {}

  showwait = false;
  AuthList:any;
  CityList:any;
  UserList: any;
  TotalCount: any;
  myDate: any;
  limitDate: any;
  isOkLoading = false;
  isAddVisible =  false;
  userinfo={
    userName:"",
    idNum:"",
    phoneNum:"",
    isCert:0,
    isReal:0,
  };
  option = {
    Page: 1,
    Number: 10,
    SearchContent: "",
  };
  onIndexChange(e) {
    this.option.Page = e;
    this.GetUserList();
  }
  ngOnInit() {
    this.AuthList = this.mySharedService.getGlobalVar();
    this.GetUserList();
  }
  showModal(info){
    this.userinfo = info;
    this.isAddVisible =  true;
  }
  handleCancel(){
    this.isAddVisible =  false;
  }
  //获取列表
  GetUserList() {
    this.showwait = true;
    let url = "api/User/GetMember";
    this.http.post(url,this.option).subscribe(
      (data) => {
        this.myDate = data;
        this.UserList = this.myDate.bigField;
        this.TotalCount = this.myDate.totalCount;
        this.showwait = false;
      },
      response => {
        this.showwait = false;
      });
  }
}
