import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { NzMessageService,UploadFile } from 'ng-zorro-antd';
import { CookieService } from 'ngx-cookie-service';
import { MySharedService } from '../../my-shared.service';
@Component({
  selector: 'app-collect',
  templateUrl: './collect.component.html',
  styleUrls: ['./collect.component.css']
})
export class CollectComponent implements OnInit {

  constructor(
    private http: HttpClient,
    private message: NzMessageService,
    private cookieService: CookieService,
    private mySharedService: MySharedService
  ) {}

  showwait = false;
  AuthList:any;
  CityList:any;
  UserCollectList: any;
  TotalCount: any;
  myDate: any;
  limitDate: any;
  isOkLoading = false;

  option = {
    Page: 1,
    Number: 10,
    SearchContent: "",
  };
  onIndexChange(e) {
    this.option.Page = e;
    this.GetUserCollectList();
  }
  ngOnInit() {
    this.AuthList = this.mySharedService.getGlobalVar();
    this.GetUserCollectList();
  }
  //获取列表
  GetUserCollectList() {
    this.showwait = true;
    let url = "api/UserCollect/GetUserCollects";
    this.http.post(url,this.option).subscribe(
      (data) => {
        this.myDate = data;
        this.UserCollectList = this.myDate.bigField;
        this.TotalCount = this.myDate.totalCount;
        this.showwait = false;
      },
      response => {
        this.showwait = false;
      });
  }

}
