import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { NzMessageService } from 'ng-zorro-antd';
import { HttpClient, HttpHeaders, HttpRequest, HttpResponse } from '@angular/common/http';
import { CookieService } from 'ngx-cookie-service';
import { MySharedService } from '../../my-shared.service'

@Component({
  selector: 'app-workspace',
  templateUrl: './workspace.component.html',
  styleUrls: ['/workspace.component.scss']
})
export class WorkspaceComponent implements OnInit {
  //超管权限
  AuthList:any;
  isCollapsed = false;
  type = "logo";
  isAddVisible = false;

  UserAccount = "";
  UserLogo = "assets/img/altlogo.png";
  UserName = "";
  MerObj = {
    Id: 0,
    Password: "",
    SurePassword: ""
  };


  //步骤条
  current = 0;
  step1 = true;
  step2 = false;


  constructor(
    public router: Router,
    private message: NzMessageService,
    private http: HttpClient,
    private cookieService: CookieService,
    private mySharedService: MySharedService
  ) { }

  ngOnInit() {
    this.AuthList = this.mySharedService.getGlobalVar();
    console.log(this.AuthList);
    this.CheckTypeUser();
  }
  CheckTypeUser() {    
    if (this.cookieService.get('badun_sys_Token') != "") {
      this.MerObj.Id = parseInt(this.cookieService.get('badun_sys_Id')); //将系统用户Id赋值
      this.UserAccount = this.cookieService.get( 'badun_sys_Account');
      if(this.cookieService.get('badun_sys_Logo') ==  "" || this.cookieService.get('badun_sys_Logo') ==  'null'){
        this.UserLogo =  "assets/img/altlogo.png";
      }else{
        this.UserLogo =  this.cookieService.get('badun_sys_Logo');
      }
      this.UserName = this.cookieService.get( 'badun_sys_Name');


      //获取某一系统用户权限列表
      let url = "api/Role/GetOneRoleLimit?id=" + parseInt(this.cookieService.get('badun_sys_RoleId'));
      this.http.get(url).subscribe(
        data =>{
          this.mySharedService.updateGlobalVar(data);
          console.log(this.mySharedService.getGlobalVar(),"666");
          this.AuthList = this.mySharedService.getGlobalVar();
        },
        err => {

        })    
    }
    else {
      this.cookieService.deleteAll();
      this.router.navigateByUrl("login");
      this.message.info('登录后继续访问！');
    }
  }
  changelogo() {
    this.isCollapsed = !this.isCollapsed;
    this.type = this.isCollapsed ? "minlogo" : "logo";
  }
  cancel(): void {
    //this.message.info('click cancel');
  }
  //注销
  confirm(): void {
    this.cookieService.deleteAll();
    this.router.navigateByUrl("login");
    this.message.info('请您登录后继续访问！');
  }
  //修改密码
  showAddModal(): void {
    this.isAddVisible = true;
  }
  handleAddCancel(): void {
    this.isAddVisible = false;
    this.MerObj.Password = "";
    this.current = 0;
    this.step1 = true;
    this.step2 = false;
  }
  //确认修改密码
  ModifyPassword() {
    let url = "api/Sys_User/InitSysPassword";    
    if (this.MerObj.Password != this.MerObj.SurePassword) {
      this.message.error("两次密码不一致")
    } else {
      this.http.post(url, this.MerObj).subscribe(
        (val) => {
          this.message.success("修改成功")
          this.router.navigateByUrl("login")
          this.isAddVisible = true;
          this.current = 0;
        },
        (response) => {
          this.message.error(response)
          this.isAddVisible = true;
        });
    }
  }
  //检查原密码正确与否
  CheckPassword() {
    let url = "api/Sys_User/CheckPassword";;
    this.http.post(url, this.MerObj).subscribe(
      (val) => {
        this.current = 1;
        this.MerObj.Password = "";
        this.changeContent();
      },
      response => {
          this.message.error(response)
      });
  }
  //模态框确定按钮
  sureBtn(): void {

    if (this.current == 0) {
      this.CheckPassword();
    } else {
      this.ModifyPassword();
    }
  }
  //步骤条
  changeContent(): void {
    switch (this.current) {
      case 0: {
        this.step1 = true;
        this.step2 = false;
        break;
      }
      case 1: {
        this.step1 = false;
        this.step2 = true;
        break;
      }
      default: {
        this.step1 = true;
        this.step2 = false;
      }
    }
  }

}
