import { Component, OnInit } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { NzFormatEmitEvent, NzTreeComponent, NzTreeNode,NzMessageService,UploadFile } from 'ng-zorro-antd';
import { CookieService } from 'ngx-cookie-service';
import { MySharedService } from '../../my-shared.service';
import * as endOfMonth from 'date-fns/end_of_month';
import * as startOfMonth from 'date-fns/start_of_month';
import * as endOfDay from 'date-fns/end_of_day';
import * as startOfDay from 'date-fns/start_of_day';

@Component({
  selector: 'app-order-merchant',
  templateUrl: './order-merchant.component.html',
  styleUrls: ['./order-merchant.component.css']
})
export class OrderMerchantComponent implements OnInit {

  constructor(
    private http: HttpClient,
    private message: NzMessageService,
    private cookieService: CookieService,
    private mySharedService: MySharedService
  ) {}

  ngOnInit() {
    this.GetAllFactory();
  }
  option = {
    Page: 1,
    Number: 10,
    MerchantId: 0,
    Orderstate:"全部订单",
    BeginDate:new Date(),
    EndDate:new Date()
  };
  mydata:any;
  OrderList=[];
  TotalCount: any;
  TotalMoney:any;
  IsAllPay = false;
  ranges1 = { '今天': [ startOfDay(new Date()), endOfDay(new Date()) ], '本月': [ startOfDay(startOfMonth(new Date())), endOfDay(endOfMonth(new Date())) ] };
  OrderStates = ["全部订单","待发货","已发货","已完成"];
  //选择不同日期段
  onChange(result: Date[]): void {
   
    this.option.BeginDate = startOfDay(result[0]);
    this.option.EndDate = endOfDay(result[1]);
    console.log('From: ', this.option.BeginDate , ', to: ',  this.option.EndDate );
  }
  //选择不同订单状态
  ChooseState(data){
    this.option.Orderstate = data;
  }
  onIndexChange(e) {
    this.option.Page = e;
    this.GetOrderList();
  }
  AllMerchantList = [];
  //获取所有商家家列表
  GetAllFactory(){
    let url = "api/Merchant/GetAllMerchant";
    this.http.get(url).subscribe(
      (data:any) => {
        this.AllMerchantList = data;
      },
      (response) => {
        console.log(response);        
        this.message.create("error", response, { nzDuration: 3000 });
      });
  }
  initSelectGet = "请选择要查询的商家";
  //选择不同商家查看商品列表
  ChooseMerchantGet(merchant){
    this.option.MerchantId = merchant.id;
    this.initSelectGet = merchant.idName;
    this.option.MerchantId = merchant.id;
  }
    //获取订单列表
  GetOrderList() {
    let url = "api/Order/GetOrdersMer";
    this.http.post(url, this.option).subscribe(
      (data) => {
        this.mydata = data;
        console.log(data,"订单");
        this.OrderList = this.mydata.bigField;
        this.TotalCount = this.mydata.totalCount;
        this.TotalMoney = this.mydata.totalMoney;
        this.IsAllPay = this.mydata.isAllPay;
      },
      (response) => {
        console.log(response);        
        this.message.create("error", response, { nzDuration: 3000 });
      });
  }
    //发货
    SureTo3(id) {
      let url = "api/Order/EditOderto3?Id=" + id;
      this.http.get(url).subscribe(
        (data) => {
          this.GetOrderList();
          this.message.create("success", data.toString(), { nzDuration: 3000 });
        },
        (response) => {
          console.log(response);
          this.message.create("error", response, { nzDuration: 3000 });
        });
    }
}
