import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { NzMessageService } from 'ng-zorro-antd';
import { CookieService } from 'ngx-cookie-service';
import { MySharedService } from '../../my-shared.service';

@Component({
  selector: 'app-evaluate',
  templateUrl: './evaluate.component.html',
  styleUrls: ['./evaluate.component.css']
})
export class EvaluateComponent implements OnInit {

  constructor(
    private http: HttpClient,
    private message: NzMessageService,
    private cookieService: CookieService,
    private mySharedService: MySharedService
  ) {}
  option = {
    Page: 1,
    Number: 10,
    SearchContent: "",
  };
  showwait = false;
  AuthList:any;
  TotalCount: any;
  HouseEvaluateList: any;
  myDate: any;
  limitDate: any;
  ngOnInit() {
    this.AuthList = this.mySharedService.getGlobalVar();
    this.GetHouseEvaluateList();
  }
  //获取列表
  GetHouseEvaluateList() {
    this.showwait = true;
    let url = "api/HouseEvaluate/GetHouseEvaluates";
    this.http.post(url,this.option).subscribe(
      (data) => {
        this.myDate = data;
        console.log(data);
        this.HouseEvaluateList = this.myDate.bigField;
        this.TotalCount = this.myDate.totalCount;
        this.showwait = false;
      },
      response => {
        this.showwait = false;
      });
  }
  //删除
  del(id) {
    let url = "api/HouseEvaluate/Delete?Id=" + id;
    this.http.delete(url).subscribe(
      (val) => {
        this.GetHouseEvaluateList();
        this.message.create("success", val.toString(), { nzDuration: 3000 });
      },
      response => {
        this.message.create("error", response, { nzDuration: 3000 });
      });
  }

}
