import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { NzMessageService,UploadFile } from 'ng-zorro-antd';
import { CookieService } from 'ngx-cookie-service';
import { MySharedService } from '../../my-shared.service';

@Component({
  selector: 'app-device',
  templateUrl: './device.component.html',
  styleUrls: ['./device.component.css']
})
export class DeviceComponent implements OnInit {

  constructor(
    private http: HttpClient,
    private message: NzMessageService,
    private cookieService: CookieService,
    private mySharedService: MySharedService
  ) {}

  showwait = false;
  AuthList:any;
  DeviceList: any;
  myDate: any;
  limitDate: any;
  isAddVisible = false;
  isModifyVisible = false;
  isOkLoading = false;
  AddDeviceObj = {
    Content: "",
    Logo: "",
    Name: "",
  };
  ModifyObj = {
    logo:"",
  };
  ngOnInit() {
    this.AuthList = this.mySharedService.getGlobalVar();
    this.GetDeviceList();
  }
  AppletList:any;
  //获取列表
  GetDeviceList() {
    this.showwait = true;
    let url = "api/Device/GetDevices";
    this.http.get(url).subscribe(
      (data) => {
        this.myDate = data;
        console.log(data);
        this.DeviceList = this.myDate;
        this.showwait = false;
      },
      response => {
        this.showwait = false;
      });
  }
  //新增模态框
  showAddModal(): void {
    this.isAddVisible = true;
    this.AddDeviceObj = {
      Content: "",
      Logo: "",
      Name: "",
    };
  }
  handleCancel(): void {
    this.isAddVisible = false;
  }
  //新增
  AddDevice() {
    this.isOkLoading = true;
    let url = "api/Device/Create";
    this.http.post(url, this.AddDeviceObj).subscribe(
      (val) => {
        this.GetDeviceList();
        this.isAddVisible = false;
        this.isOkLoading = false;
        this.message.create("success", val.toString(), { nzDuration: 3000 });     
      },
      response => {
        this.isAddVisible = false;
        this.isOkLoading = false;
        this.message.create("error", response, { nzDuration: 3000 });
      });
  }
  //编辑模态框
  showModifyModal(obj) {
    this.isModifyVisible = true;
    this.ModifyObj = obj;
    console.log(this.ModifyObj);
  }
  handleModifyCancel(): void {
    this.isModifyVisible = false;
  }
  //修改
  ModifyDevice() {
    console.log(this.ModifyObj);
    this.isOkLoading = true;
    let url = "api/Device/Modify";
    this.http.post(url, this.ModifyObj).subscribe(
      (val) => {
        this.GetDeviceList();
        this.isModifyVisible = false;
        this.isOkLoading = false;
        this.message.create("success", val.toString(), { nzDuration: 3000 });
      },
      response => {
        this.isModifyVisible = false;
        this.isOkLoading = false;
        this.message.create("error", response, { nzDuration: 3000 });
      });
  }
  //删除
  del(id) {
    let url = "api/Device/Delete?Id=" + id;
    this.http.delete(url).subscribe(
      (val) => {
        this.GetDeviceList();
        this.message.create("success", val.toString(), { nzDuration: 3000 });
      },
      response => {
        this.message.create("error", response, { nzDuration: 3000 });
      });
  }
    //上传logo
    fileToUpload: File = null;//文件上传
    logoUrl = "api/PostImg/PostProfilePicture?path=devicelogo";
    loading = false;
    UpLoadSpinning = false;//上传图片等待spin
    //logo图片上传
    beforeUpload = (file: File) => {
      const isLt2M = file.size / 1024 / 1024 < 2;
      if (!isLt2M) {
        this.message.error('图片需小于 2MB!');
      }
      this.UpLoadSpinning = true;
      return isLt2M;
    }
    handleChange(info: { file: UploadFile }): void {
      if (info.file.status === 'uploading') {
        this.loading = true;
        return;
      }
      if (info.file.status === 'done') {
        this.loading = false;
        this.UpLoadSpinning = false;
        this.AddDeviceObj.Logo = info.file.response;
        this.ModifyObj.logo = info.file.response;
      }
    }
}
