import { Component, OnInit } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { NzMessageService, UploadFile,NzFormatEmitEvent } from 'ng-zorro-antd';
import { CookieService } from 'ngx-cookie-service';
import { MySharedService } from '../../my-shared.service';

@Component({
  selector: 'app-applet',
  templateUrl: './applet.component.html',
  styleUrls: ['./applet.component.css']
})
export class AppletComponent implements OnInit {

  constructor(
    private http: HttpClient,
    private message: NzMessageService,
    private cookieService: CookieService,
    private mySharedService: MySharedService
  ) { }

  showwait = false;
  AppletList:any;
  myDate:any;
  TotalCount: any;
  option = {
    Page: 1,
    Number: 10,
  };
  isAddVisible = false;
  isModifyVisible = false;
  isOkLoading = false;
  AddObj = {
    Name: "",
    Logo:"",
    appid:"",
    secret:"",
    sub_mch_id:""
  };
  ModifyObj = {
    logo:"",
  };
  ngOnInit() {
    this.GetApplets();
  } 
  onIndexChange(e) {
    this.option.Page = e;
    this.GetApplets();
  }
  //获取小程序列表
  GetApplets() {
    this.showwait = true;
    let url = "api/Applet/GetApplets";
    this.http.post(url, this.option).subscribe(
      (data) => {
        this.myDate = data;
        console.log(data);
        this.AppletList = this.myDate.bigField;
        this.TotalCount = this.myDate.totalCount;
        this.showwait = false;
      },
      (response) => {
        console.log(response);        
        this.message.create("error", response, { nzDuration: 3000 });
        this.showwait = false;
      });
  }
   //新增模态框
   showAddModal(): void {
    this.isAddVisible = true;
    this.AddObj = {
      Name: "",
      Logo:"",
      appid:"",
      secret:"",
      sub_mch_id:""
    };
  }
  handleCancel(): void {
    this.isAddVisible = false;
  }
  //新增
  AddApplet() {
    this.isOkLoading = true;
    let url = "api/Applet/AddApplet";
    this.http.post(url, this.AddObj).subscribe(
      (val) => {
        this.GetApplets();
        this.isAddVisible = false;
        this.isOkLoading = false;
        this.message.create("success", val.toString(), { nzDuration: 3000 });
      },
      response => {
        this.isAddVisible = false;
        this.isOkLoading = false;
        this.message.create("error", response, { nzDuration: 3000 });
      });
  }
  //编辑模态框
  showModifyModal(obj) {
    this.isModifyVisible = true;
    this.ModifyObj = obj;
  }
  handleModifyCancel(): void {
    this.isModifyVisible = false;
  }
  //修改
  ModifyApplet() {
    console.log(this.ModifyObj);
    this.isOkLoading = true;
    let url = "api/Applet/EditApplet";
    this.http.post(url, this.ModifyObj).subscribe(
      (val) => {
        this.GetApplets();
        this.isModifyVisible = false;
        this.isOkLoading = false;
        this.message.create("success", val.toString(), { nzDuration: 3000 });
      },
      response => {
        this.isModifyVisible = false;
        this.isOkLoading = false;
        this.message.create("error", response, { nzDuration: 3000 });
      });
  }
  //删除专家
  delExpert(id) {
    let url = "api/Applet/DelExpert?Id=" + id;
    this.http.delete(url).subscribe(
      (val) => {
        this.GetApplets();
        this.message.create("success", val.toString(), { nzDuration: 3000 });
      },
      response => {
        this.message.create("error", response, { nzDuration: 3000 });
      });
  }
  //上传logo
  fileToUpload: File = null;//文件上传
  logoUrl = "api/PostImg/PostProfilePicture?path=appletlogo";
  loading = false;
  UpLoadSpinning = false;//上传图片等待spin
  //logo图片上传
  beforeUpload = (file: File) => {
    const isLt2M = file.size / 1024 / 1024 < 2;
    if (!isLt2M) {
      this.message.error('图片需小于 2MB!');
    }
    this.UpLoadSpinning = true;
    return isLt2M;
  }
  handleChange(info: { file: UploadFile }): void {
    if (info.file.status === 'uploading') {
      this.loading = true;
      return;
    }
    if (info.file.status === 'done') {
      this.loading = false;
      this.UpLoadSpinning = false;
      this.AddObj.Logo = info.file.response;
      this.ModifyObj.logo = info.file.response;
    }
  }
}
