import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { NzMessageService } from 'ng-zorro-antd';
import { CookieService } from 'ngx-cookie-service';
import { MySharedService } from '../../my-shared.service';

@Component({
  selector: 'app-notice',
  templateUrl: './notice.component.html',
  styleUrls: ['./notice.component.css']
})
export class NoticeComponent implements OnInit {

  constructor(
    private http: HttpClient,
    private message: NzMessageService,
    private cookieService: CookieService,
    private mySharedService: MySharedService
  ) {}

  showwait = false;
  AuthList:any;
  NoticeList: any;
  myDate: any;
  limitDate: any;
  isAddVisible = false;
  isModifyVisible = false;
  isOkLoading = false;
  AddNoticeObj = {
    Content: "",
    Title: "",
  };
  ModifyObj = {
   
  };
  AppletId = 0;
  ngOnInit() {
    this.AuthList = this.mySharedService.getGlobalVar();
    this.GetNoticeList();
  }
  AppletList:any;
  //获取列表
  GetNoticeList() {
    this.showwait = true;
    let url = "api/Notice/GetNotices";
    this.http.get(url).subscribe(
      (data) => {
        this.myDate = data;
        console.log(data);
        this.NoticeList = this.myDate;
        this.showwait = false;
      },
      response => {
        this.showwait = false;
      });
  }
  //新增模态框
  showAddModal(): void {
    this.isAddVisible = true;
    this.AddNoticeObj = {
      Content: "",
      Title: "",
    };
  }
  handleCancel(): void {
    this.isAddVisible = false;
  }
  //新增
  AddNotice() {
    this.isOkLoading = true;
    let url = "api/Notice/Create";
    this.http.post(url, this.AddNoticeObj).subscribe(
      (val) => {
        this.GetNoticeList();
        this.isAddVisible = false;
        this.isOkLoading = false;
        this.message.create("success", val.toString(), { nzDuration: 3000 });     
      },
      response => {
        this.isAddVisible = false;
        this.isOkLoading = false;
        this.message.create("error", response, { nzDuration: 3000 });
      });
  }
  //编辑模态框
  showModifyModal(obj) {
    this.isModifyVisible = true;
    this.ModifyObj = obj;
    console.log(this.ModifyObj);
  }
  handleModifyCancel(): void {
    this.isModifyVisible = false;
  }
  //修改
  ModifyNotice() {
    console.log(this.ModifyObj);
    this.isOkLoading = true;
    let url = "api/Notice/Modify";
    this.http.post(url, this.ModifyObj).subscribe(
      (val) => {
        this.GetNoticeList();
        this.isModifyVisible = false;
        this.isOkLoading = false;
        this.message.create("success", val.toString(), { nzDuration: 3000 });
      },
      response => {
        this.isModifyVisible = false;
        this.isOkLoading = false;
        this.message.create("error", response, { nzDuration: 3000 });
      });
  }
  //删除
  del(id) {
    let url = "api/Notice/Delete?Id=" + id;
    this.http.delete(url).subscribe(
      (val) => {
        this.GetNoticeList();
        this.message.create("success", val.toString(), { nzDuration: 3000 });
      },
      response => {
        this.message.create("error", response, { nzDuration: 3000 });
      });
  }
}
