import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import {  NzMessageService } from 'ng-zorro-antd';
import { CookieService } from 'ngx-cookie-service';
import { MySharedService } from '../../my-shared.service';
import * as endOfMonth from 'date-fns/end_of_month';
import * as startOfMonth from 'date-fns/start_of_month';
import * as endOfDay from 'date-fns/end_of_day';
import * as startOfDay from 'date-fns/start_of_day';
import { zh_CN, NzI18nService } from 'ng-zorro-antd';
@Component({
  selector: 'app-order',
  templateUrl: './order.component.html',
  styleUrls: ['./order.component.css']
})
export class OrderComponent implements OnInit {
  constructor(
    private http: HttpClient,
    private message: NzMessageService,
    private cookieService: CookieService,
    private mySharedService: MySharedService,
    private nzI18nService:NzI18nService
  ) {}

  ngOnInit() {
    this.switchLanguage();
  }
  switchLanguage() {
    this.nzI18nService.setLocale(zh_CN);
  }
  showwait = false;
  isAddVisible = false;
  isOkLoading = false;
  AddObj = {
    Amount:0,
    HouseId : 0,
    Days: 0,
    BeginDate: new Date(),
    EndDate: new Date(),
    PeopleNum: 0,
    Msg: "",
  };
   //新增模态框
  showAddModal(): void {
    this.isAddVisible = true;
    this.AddObj = {
      Amount:0,
      HouseId : 0,
      Days: 0,
      BeginDate: new Date(),
      EndDate: new Date(),
      PeopleNum: 0,
      Msg: "",
    };
  }
  handleCancel(): void {
    this.isAddVisible = false;
  }
   //新增
  AddOrder() {
    this.isOkLoading = true;
    let url = "api/Order/CreateOrderBySysUser";
    this.http.post(url, this.AddObj).subscribe(
      (val) => {
        this.GetOrderList();
        this.isAddVisible = false;
        this.isOkLoading = false;
        this.message.create("success", val.toString(), { nzDuration: 3000 });     
      },
      response => {
        this.isAddVisible = false;
        this.isOkLoading = false;
        this.message.create("error", response, { nzDuration: 3000 });
      });
  }
  option = {
    Page: 1,
    Number: 10,
    Orderstate:"全部订单",
    BeginDate:new Date(),
    EndDate:new Date(),
    State:0,
  };
  mydata:any;
  OrderList=[];
  TotalCount: 0;
  ranges1 = { '今天': [ startOfDay(new Date()), endOfDay(new Date()) ], '本月': [ startOfDay(startOfMonth(new Date())), endOfDay(endOfMonth(new Date())) ] };
  OrderStates = ["全部订单","未支付","已支付","已完成","已评价","已取消"];
  //选择不同日期段
  onChange(result: Date[]): void {
   
    this.option.BeginDate = startOfDay(result[0]);
    this.option.EndDate = endOfDay(result[1]);
    console.log('From: ', this.option.BeginDate , ', to: ',  this.option.EndDate );
  }
  //选择不同订单状态
  ChooseState(data){
    this.option.Orderstate = data;
    if(data =="全部订单"){
      this.option.State = 0;
    }
    else if(data =="未支付"){
      this.option.State = 2;
    }
    else if(data =="已支付"){
      this.option.State = 1;
    }
    else if(data =="已完成"){
      this.option.State = 3;
    }
    else if(data =="已取消"){
      this.option.State = 9;
    }
    else if(data =="已评价"){
      this.option.State = 4;
    }
    else{
      this.option.State = 0;
    }
  }
  onIndexChange(e) {
    this.option.Page = e;
    this.GetOrderList();
  }
    //获取订单列表
  GetOrderList() {
    let url = "api/Order/GetOrderList";
    this.http.post(url, this.option).subscribe(
      (data) => {
        this.mydata = data;
        this.OrderList = this.mydata.bigField;
        this.TotalCount = this.mydata.totalCount;
      },
      (response) => {
        console.log(response);        
        this.message.create("error", response, { nzDuration: 3000 });
      });
  }

}
