import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { NzMessageService,UploadFile } from 'ng-zorro-antd';
import { CookieService } from 'ngx-cookie-service';
import { MySharedService } from '../../my-shared.service';

@Component({
  selector: 'app-traveling',
  templateUrl: './traveling.component.html',
  styleUrls: ['./traveling.component.css']
})
export class TravelingComponent implements OnInit {

  constructor(
    private http: HttpClient,
    private message: NzMessageService,
    private cookieService: CookieService,
    private mySharedService: MySharedService
  ) {}

  showwait = false;
  AuthList:any;
  CityList:any;
  TravelingList: any;
  TotalCount: any;
  myDate: any;
  limitDate: any;
  isAddVisible = false;
  isModifyVisible = false;
  isOkLoading = false;
  AddTravelingObj = {
    Content: "",
    Logo: "",
    Title: "",
    Banner: "",
    CityId: 0,
    CityName: "",
    ClickNum: 0,
    IsHot: true,
    Video: "",
    ZanNum: 0,
    WeightValue: "",
  };
  ModifyObj = {
    logo:"",
    isHot:true,
  };
  option = {
    Page: 1,
    Number: 10,
    SearchContent: "",
  };
  onIndexChange(e) {
    this.option.Page = e;
    this.GetTravelingList();
  }
  ngOnInit() {
    this.AuthList = this.mySharedService.getGlobalVar();
    this.GetCityList();
    this.GetTravelingList();
  }
  AppletList:any;
  //获取城市列表
  GetCityList() {
    this.showwait = true;
    let url = "api/City/GetCitys";
    this.http.get(url).subscribe(
      (data) => {         
        this.CityList = data;
        console.log(this.CityList);
        this.showwait = false;
      },
      response => {
        this.showwait = false;
      });
  }
  //获取列表
  GetTravelingList() {
    this.showwait = true;
    let url = "api/Traveling/GetTravelings";
    this.http.post(url,this.option).subscribe(
      (data) => {
        this.myDate = data;
        this.TravelingList = this.myDate.bigField;
        this.TotalCount = this.myDate.totalCount;
        this.showwait = false;
      },
      response => {
        this.showwait = false;
      });
  }
  change1(){    
    this.AddTravelingObj.IsHot = !this.AddTravelingObj.IsHot;
  }
  change2(){    
    this.ModifyObj.isHot = !this.ModifyObj.isHot;
  }
  //新增模态框
  showAddModal(): void {
    this.isAddVisible = true;
    this.AddTravelingObj = {
      Content: "",
      Logo: "",
      Title: "",
      Banner: "",
      CityId: 0,
      CityName: "",
      ClickNum: 0,
      IsHot: true,
      Video: "",
      ZanNum: 0,
      WeightValue: "",
    };
  }
  handleCancel(): void {
    this.isAddVisible = false;
  }
  //新增
  AddTraveling() {
    this.isOkLoading = true;
    let url = "api/Traveling/Create";
    this.http.post(url, this.AddTravelingObj).subscribe(
      (val) => {
        this.GetTravelingList();
        this.isAddVisible = false;
        this.isOkLoading = false;
        this.message.create("success", val.toString(), { nzDuration: 3000 });     
      },
      response => {
        this.isAddVisible = false;
        this.isOkLoading = false;
        this.message.create("error", response, { nzDuration: 3000 });
      });
  }
  //编辑模态框
  showModifyModal(obj) {
    this.isModifyVisible = true;
    this.ModifyObj = obj;
    console.log(this.ModifyObj);
  }
  handleModifyCancel(): void {
    this.isModifyVisible = false;
  }
  //修改
  ModifyTraveling() {
    console.log(this.ModifyObj);
    this.isOkLoading = true;
    let url = "api/Traveling/Modify";
    this.http.post(url, this.ModifyObj).subscribe(
      (val) => {
        this.GetTravelingList();
        this.isModifyVisible = false;
        this.isOkLoading = false;
        this.message.create("success", val.toString(), { nzDuration: 3000 });
      },
      response => {
        this.isModifyVisible = false;
        this.isOkLoading = false;
        this.message.create("error", response, { nzDuration: 3000 });
      });
  }
  //删除
  del(id) {
    let url = "api/Traveling/Delete?Id=" + id;
    this.http.delete(url).subscribe(
      (val) => {
        this.GetTravelingList();
        this.message.create("success", val.toString(), { nzDuration: 3000 });
      },
      response => {
        this.message.create("error", response, { nzDuration: 3000 });
      });
  }
    //上传logo
    fileToUpload: File = null;//文件上传
    logoUrl = "api/PostImg/PostProfilePicture?path=travelinglogo";
    loading = false;
    UpLoadSpinning = false;//上传图片等待spin
    //logo图片上传
    beforeUpload = (file: File) => {
      const isLt2M = file.size / 1024 / 1024 < 2;
      if (!isLt2M) {
        this.message.error('图片需小于 2MB!');
      }
      this.UpLoadSpinning = true;
      return isLt2M;
    }
    handleChange(info: { file: UploadFile }): void {
      if (info.file.status === 'uploading') {
        this.loading = true;
        return;
      }
      if (info.file.status === 'done') {
        this.loading = false;
        this.UpLoadSpinning = false;
        this.AddTravelingObj.Logo = info.file.response;
        this.ModifyObj.logo = info.file.response;
      }
    }

}
