import { Component, OnInit } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { NzMessageService, NzTreeNode } from 'ng-zorro-antd';
import { NzFormatEmitEvent } from 'ng-zorro-antd';
import { longStackSupport } from 'q';
import { CookieService } from 'ngx-cookie-service';
import { MySharedService } from '../../my-shared.service';

@Component({
  selector: 'app-sys-role',
  templateUrl: './sys-role.component.html',
  styleUrls: ['./sys-role.component.css']
})
export class SysRoleComponent implements OnInit {

  constructor(
    private http: HttpClient,
    private message: NzMessageService,
    private cookieService: CookieService,
    private mySharedService: MySharedService
  ) { }

  showwait = false;
  LimitList:any;
  RoleList:any;
  myDate:any;
  TotalCount: any;
  option = {
    SearchContent:"",
    Page: 1,
    Number: 10,
  };
  isAddVisible = false;
  isModifyVisible = false;
  isOkLoading = false;
  AddRoleObj = {
    Name: "",
    AppletName:"",
    CheckedArray: []
  };
  ModifyObj ={
    RoleId:0,
    Name: "",
    CheckedArray: []
  };
  ngOnInit() {
    this.GetRoles();

    this.GetLimitList();
  }
  onIndexChange(e) {
    this.option.Page = e;
    this.GetRoles();
  }
  AppletList:any;

  //获取角色列表
  GetRoles() {
    this.showwait = true;
    let url = "api/Role/GetRoles";
    this.http.post(url, this.option).subscribe(
      (data) => {
        this.myDate = data;
        console.log(data);
        this.RoleList = this.myDate.bigField;
        this.TotalCount = this.myDate.totalCount;
        this.showwait = false;
      },
      (response) => {
        console.log(response);        
        this.message.create("error", response, { nzDuration: 3000 });
        this.showwait = false;
      });
  }
  //新增模态框
  showAddModal(): void {
    this.isAddVisible = true;
    this.AddRoleObj = {
      Name: "",
      AppletName:"",
      CheckedArray: []
    };
    this.GetLimitList();
  }
  handleCancel(): void {
    this.isAddVisible = false;
  }
  //新增角色
  AddRole() {
    console.log(this.AddRoleObj);
    this.isOkLoading = true;
    let url = "api/Role/CreateRole";
    this.http.post(url, this.AddRoleObj).subscribe(
      (val) => {
        this.GetRoles();
        this.isAddVisible = false;
        this.isOkLoading = false;
        this.message.create("success", val.toString(), { nzDuration: 3000 }); 
      },
      response => {
        this.isAddVisible = false;
        this.isOkLoading = false;
        this.message.create("error", response, { nzDuration: 3000 });
      });
  }
    //编辑模态框
    showModifyModal(obj){
      this.isModifyVisible = true;
      this.ModifyObj = obj;
      this.GetOneLimitList(obj.id);
    }
    handleModifyCancel(): void {
      this.isModifyVisible = false;
    }
  //修改角色
  ModifyRole() {
    console.log(this.ModifyObj);
    this.isOkLoading = true;
    let url = "api/Role/EditRole";
    this.http.post(url, this.ModifyObj).subscribe(
      (val) => {
        this.GetRoles();
        this.isModifyVisible = false;
        this.isOkLoading = false;
        this.message.create("success", val.toString(), { nzDuration: 3000 });
      },
      response => {
        this.isModifyVisible = false;
        this.isOkLoading = false;
        this.message.create("error", response, { nzDuration: 3000 });
      });
  }
    //删除角色
    delrole(id){
      let url = "api/Role/DeleteRole?id="+id;
      this.http.delete(url).subscribe(
       (val) => {
         this.message.create("success", val.toString(),{ nzDuration: 3000 });
         this.GetRoles();
       },
       response => {
         this.message.create("error", response,{ nzDuration: 3000 });
       });
   }


       //--------------------初始新增树图-----------------------------------------
  // expandKeys = ['1001', '10001'];
  Childrens = [];
 // empList: Array<Custom> = [];
  // selectedKeys = ['10001', '100011'];
  expandDefault = false;
  nodes = [];
  mouseAction(name: string, event: NzFormatEmitEvent): void {
    if(event.node.level === 0){
      this.Childrens = event.node.children;
    }
    if(event.node.level === 1){
      this.Childrens = event.node.parentNode.children;
    }
    if(event.node.level === 2){
      this.Childrens = event.node.parentNode.parentNode.children;
    }
    const  myChecked = [];
    this.Childrens.forEach(function(data){
      data.children.forEach(function(data){
        if(data.isChecked == true){
          myChecked.push({key:data.key});
        }
      })
    })
    
    this.AddRoleObj.CheckedArray = myChecked;
    console.log(this.AddRoleObj.CheckedArray);
  }
  //获取权限树
  GetLimitList() {
    let url = "api/Limit/GetLimitTree";
    this.http.get(url).subscribe(
      (data: any[]) => {
        console.log(data);
        this.nodes = [
          new NzTreeNode(
            {
              title: '系统权限分配',
              key:"1",
              expanded: true,
              children: data
            }
          )

        ];
        console.log(this.nodes);
      },
      response => {
        this.message.create("error", response,{ nzDuration: 3000 });
      });
  }
  //获取某一角色权限树
  GetOneLimitList(roleId) {
    let url = "api/Limit/GetOneLimitTree?id=" + roleId;
    this.http.get(url).subscribe(
      (data: any[]) => {
        console.log(data);
        this.nodes = [
          new NzTreeNode(
            {
              title: '系统权限分配',
              key:'1',
              expanded: true,
              children: data
            }
          )

        ];
        console.log(this.nodes);
      },
      response => {
        this.message.create("error", response,{ nzDuration: 3000 });
      });
  }
  mouseAction1(name: string, event: NzFormatEmitEvent): void {
    if(event.node.level === 0){
      this.Childrens = event.node.children;
    }
    if(event.node.level === 1){
      this.Childrens = event.node.parentNode.children;
    }
    if(event.node.level === 2){
      this.Childrens = event.node.parentNode.parentNode.children;
    }
    const  myChecked = [];
    this.Childrens.forEach(function(data){
      data.children.forEach(function(data){
        if(data.isChecked == true){
          myChecked.push({key:data.key});
        }
      })
    })
this.ModifyObj.CheckedArray = myChecked;
    console.log(this.ModifyObj.CheckedArray);
  }
  //----------------------------------------------------------------------

}
