import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';

import { FormsModule } from '@angular/forms';
import { HttpClientModule,HTTP_INTERCEPTORS } from '@angular/common/http';
import { NgZorroAntdModule } from 'ng-zorro-antd';

import { RouterModule } from '@angular/router';
import { workspaceRoutes } from './workspace.routes';

import { WorkspaceComponent } from './workspace.component';

import { HomeComponent } from '../../bs-modules/home/home.component';
import { OrderComponent } from '../../bs-modules/order/order.component';
import { OrderMerchantComponent } from '../../bs-modules/order-merchant/order-merchant.component';


import { AppletComponent } from '../../bs-modules/applet/applet.component';
import { SysUserComponent } from '../../bs-modules/sys-user/sys-user.component';
import { SysRoleComponent } from '../../bs-modules/sys-role/sys-role.component';
import { HouseComponent } from '../../bs-modules/house/house.component';
import { HouseOwnerComponent } from '../../bs-modules/house-owner/house-owner.component';
import { UserComponent } from '../../bs-modules/user/user.component';
import { DeviceComponent } from '../../bs-modules/device/device.component';
import { ServiceComponent } from '../../bs-modules/service/service.component';
import { NoticeComponent } from '../../bs-modules/notice/notice.component';
import { UnsubComponent } from '../../bs-modules/unsub/unsub.component';
import { AdbannerComponent } from '../../bs-modules/adbanner/adbanner.component';
import { CityComponent } from '../../bs-modules/city/city.component';
import { CollectComponent } from '../../bs-modules/collect/collect.component';
import { IntegralComponent } from '../../bs-modules/integral/integral.component';
import { EvaluateComponent } from '../../bs-modules/evaluate/evaluate.component';
import { DiscountComponent } from '../../bs-modules/discount/discount.component';
import { HousefoodComponent } from '../../bs-modules/housefood/housefood.component';
import { TravelingComponent } from '../../bs-modules/traveling/traveling.component';

import { MySharedService } from '../../my-shared.service';
import { MyHttpInterceptor } from '../../my-http-interceptor';


@NgModule({
    imports: [
        CommonModule,
        FormsModule,
        HttpClientModule,
        NgZorroAntdModule.forRoot(),
        RouterModule.forChild(workspaceRoutes)
    ],
    exports: [],
    declarations: [
        WorkspaceComponent,
        HomeComponent,
        OrderComponent,
        OrderMerchantComponent,

        AppletComponent,
        SysUserComponent,
        SysRoleComponent,
        HouseComponent,
        HouseOwnerComponent,
        UserComponent,
        DeviceComponent,
        ServiceComponent,
        NoticeComponent,
        UnsubComponent,
        AdbannerComponent,
        CityComponent,
        CollectComponent,
        IntegralComponent,
        EvaluateComponent,
        DiscountComponent,
        HousefoodComponent,
        TravelingComponent,
    ],
    providers: [
        MySharedService,
        { provide: HTTP_INTERCEPTORS, useClass: MyHttpInterceptor, multi: true  }
    ]
})
export class WorkspaceModule { }