import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { NzMessageService } from 'ng-zorro-antd';
import { CookieService } from 'ngx-cookie-service';
import { MySharedService } from '../../my-shared.service';
@Component({
  selector: 'app-discount',
  templateUrl: './discount.component.html',
  styleUrls: ['./discount.component.css']
})
export class DiscountComponent implements OnInit {

  constructor(
    private http: HttpClient,
    private message: NzMessageService,
    private cookieService: CookieService,
    private mySharedService: MySharedService
  ) {}

  showwait = false;
  AuthList:any;
  DiscountList: any;
  myDate: any;
  limitDate: any;
  isAddVisible = false;
  isModifyVisible = false;
  isOkLoading = false;
  AddDiscountObj = {
    Days: "",
    DisValue: "",
  };
  ModifyObj = {};
  ngOnInit() {
    this.AuthList = this.mySharedService.getGlobalVar();
    this.GetDiscountList();
  }
  //获取列表
  GetDiscountList() {
    this.showwait = true;
    let url = "api/Discount/GetDiscounts";
    this.http.get(url).subscribe(
      (data) => {
        this.myDate = data;
        console.log(data);
        this.DiscountList = this.myDate;
        this.showwait = false;
      },
      response => {
        this.showwait = false;
      });
  }
  //新增模态框
  showAddModal(): void {
    this.isAddVisible = true;
    this.AddDiscountObj = {
      Days: "",
      DisValue: "",
    };
  }
  handleCancel(): void {
    this.isAddVisible = false;
  }
  //新增
  AddDiscount() {
    this.isOkLoading = true;
    let url = "api/Discount/Create";
    this.http.post(url, this.AddDiscountObj).subscribe(
      (val) => {
        this.GetDiscountList();
        this.isAddVisible = false;
        this.isOkLoading = false;
        this.message.create("success", val.toString(), { nzDuration: 3000 });     
      },
      response => {
        this.isAddVisible = false;
        this.isOkLoading = false;
        this.message.create("error", response, { nzDuration: 3000 });
      });
  }
  //编辑模态框
  showModifyModal(obj) {
    this.isModifyVisible = true;
    this.ModifyObj = obj;
    console.log(this.ModifyObj);
  }
  handleModifyCancel(): void {
    this.isModifyVisible = false;
  }
  //修改
  ModifyDiscount() {
    console.log(this.ModifyObj);
    this.isOkLoading = true;
    let url = "api/Discount/Modify";
    this.http.post(url, this.ModifyObj).subscribe(
      (val) => {
        this.GetDiscountList();
        this.isModifyVisible = false;
        this.isOkLoading = false;
        this.message.create("success", val.toString(), { nzDuration: 3000 });
      },
      response => {
        this.isModifyVisible = false;
        this.isOkLoading = false;
        this.message.create("error", response, { nzDuration: 3000 });
      });
  }
  //删除
  del(id) {
    let url = "api/Discount/Delete?Id=" + id;
    this.http.delete(url).subscribe(
      (val) => {
        this.GetDiscountList();
        this.message.create("success", val.toString(), { nzDuration: 3000 });
      },
      response => {
        this.message.create("error", response, { nzDuration: 3000 });
      });
  }

}
