import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { NzMessageService,UploadFile } from 'ng-zorro-antd';
import { CookieService } from 'ngx-cookie-service';
import { MySharedService } from '../../my-shared.service';

@Component({
  selector: 'app-adbanner',
  templateUrl: './adbanner.component.html',
  styleUrls: ['./adbanner.component.css']
})
export class AdbannerComponent implements OnInit {

  constructor(
    private http: HttpClient,
    private message: NzMessageService,
    private cookieService: CookieService,
    private mySharedService: MySharedService
  ) {}

  showwait = false;
  AuthList:any;
  AdBannerList: any;
  myDate: any;
  limitDate: any;
  isAddVisible = false;
  isModifyVisible = false;
  isOkLoading = false;
  AddAdBannerObj = {
    PicContent: "",
    PicPath: "",
    WeightValue: "",
  };
  ModifyObj = {
    picPath:"",
  };
  ngOnInit() {
    this.AuthList = this.mySharedService.getGlobalVar();
    this.GetAdBannerList();
  }
  //获取列表
  GetAdBannerList() {
    this.showwait = true;
    let url = "api/AdBanner/GetAdBanners";
    this.http.get(url).subscribe(
      (data) => {
        this.myDate = data;
        console.log(data);
        this.AdBannerList = this.myDate;
        this.showwait = false;
      },
      response => {
        this.showwait = false;
      });
  }
  //新增模态框
  showAddModal(): void {
    this.isAddVisible = true;
    this.AddAdBannerObj = {
      PicContent: "",
      PicPath: "",
      WeightValue: "",
    };
  }
  handleCancel(): void {
    this.isAddVisible = false;
  }
  //新增
  AddAdBanner() {
    this.isOkLoading = true;
    let url = "api/AdBanner/Create";
    this.http.post(url, this.AddAdBannerObj).subscribe(
      (val) => {
        this.GetAdBannerList();
        this.isAddVisible = false;
        this.isOkLoading = false;
        this.message.create("success", val.toString(), { nzDuration: 3000 });     
      },
      response => {
        this.isAddVisible = false;
        this.isOkLoading = false;
        this.message.create("error", response, { nzDuration: 3000 });
      });
  }
  //编辑模态框
  showModifyModal(obj) {
    this.isModifyVisible = true;
    this.ModifyObj = obj;
    console.log(this.ModifyObj);
  }
  handleModifyCancel(): void {
    this.isModifyVisible = false;
  }
  //修改
  ModifyAdBanner() {
    console.log(this.ModifyObj);
    this.isOkLoading = true;
    let url = "api/AdBanner/Modify";
    this.http.post(url, this.ModifyObj).subscribe(
      (val) => {
        this.GetAdBannerList();
        this.isModifyVisible = false;
        this.isOkLoading = false;
        this.message.create("success", val.toString(), { nzDuration: 3000 });
      },
      response => {
        this.isModifyVisible = false;
        this.isOkLoading = false;
        this.message.create("error", response, { nzDuration: 3000 });
      });
  }
  //删除
  del(id) {
    let url = "api/AdBanner/Delete?Id=" + id;
    this.http.delete(url).subscribe(
      (val) => {
        this.GetAdBannerList();
        this.message.create("success", val.toString(), { nzDuration: 3000 });
      },
      response => {
        this.message.create("error", response, { nzDuration: 3000 });
      });
  }
    //上传logo
    fileToUpload: File = null;//文件上传
    logoUrl = "api/PostImg/PostProfilePicture?path=adbannerlogo";
    loading = false;
    UpLoadSpinning = false;//上传图片等待spin
    //logo图片上传
    beforeUpload = (file: File) => {
      const isLt2M = file.size / 1024 / 1024 < 2;
      if (!isLt2M) {
        this.message.error('图片需小于 2MB!');
      }
      this.UpLoadSpinning = true;
      return isLt2M;
    }
    handleChange(info: { file: UploadFile }): void {
      if (info.file.status === 'uploading') {
        this.loading = true;
        return;
      }
      if (info.file.status === 'done') {
        this.loading = false;
        this.UpLoadSpinning = false;
        this.AddAdBannerObj.PicPath = info.file.response;
        this.ModifyObj.picPath = info.file.response;
      }
    }

}
