import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { NzMessageService,UploadFile } from 'ng-zorro-antd';
import { CookieService } from 'ngx-cookie-service';
import { MySharedService } from '../../my-shared.service';

@Component({
  selector: 'app-city',
  templateUrl: './city.component.html',
  styleUrls: ['./city.component.css']
})
export class CityComponent implements OnInit {

  constructor(
    private http: HttpClient,
    private message: NzMessageService,
    private cookieService: CookieService,
    private mySharedService: MySharedService
  ) {}

  showwait = false;
  AuthList:any;
  CityList: any;
  myDate: any;
  limitDate: any;
  isAddVisible = false;
  isModifyVisible = false;
  isOkLoading = false;
  AddCityObj = {
    Name: "",
    Key: "",
    IsHot:false,
    WeightValue: "",
  };
  ModifyObj = {
    isHot:false
  };
  AppletId = 0;
  ngOnInit() {
    this.AuthList = this.mySharedService.getGlobalVar();
    this.GetCityList();
  }
  //获取
  GetCityList() {
    this.showwait = true;
    let url = "api/City/GetCitys";
    this.http.get(url).subscribe(
      (data) => {
        this.myDate = data;
        console.log(data);
        this.CityList = this.myDate;
        this.showwait = false;
      },
      response => {
        this.showwait = false;
      });
  }
  //新增模态框
  showAddModal(): void {
    this.isAddVisible = true;
    this.AddCityObj = {
      Name: "",
      Key: "",
      IsHot:false,
      WeightValue: "",
    };
  }
  handleCancel(): void {
    this.isAddVisible = false;
  }
  change(){    
    this.AddCityObj.IsHot = !this.AddCityObj.IsHot;
    console.log(1,this.AddCityObj.IsHot);
  }
  change2(){    
    this.ModifyObj.isHot = !this.ModifyObj.isHot;
    console.log(1,this.AddCityObj.IsHot);
  }
  //新增
  AddService() {
    this.isOkLoading = true;
    let url = "api/City/Create";
    this.http.post(url, this.AddCityObj).subscribe(
      (val) => {
        this.GetCityList();
        this.isAddVisible = false;
        this.isOkLoading = false;
        this.message.create("success", val.toString(), { nzDuration: 3000 });     
      },
      response => {
        this.isAddVisible = false;
        this.isOkLoading = false;
        this.message.create("error", response, { nzDuration: 3000 });
      });
  }
  //编辑模态框
  showModifyModal(obj) {
    this.isModifyVisible = true;
    this.ModifyObj = obj;
    console.log(this.ModifyObj);
  }
  handleModifyCancel(): void {
    this.isModifyVisible = false;
  }
  //修改
  Modify() {
    console.log(this.ModifyObj);
    this.isOkLoading = true;
    let url = "api/City/Modify";
    this.http.post(url, this.ModifyObj).subscribe(
      (val) => {
        this.GetCityList();
        this.isModifyVisible = false;
        this.isOkLoading = false;
        this.message.create("success", val.toString(), { nzDuration: 3000 });
      },
      response => {
        this.isModifyVisible = false;
        this.isOkLoading = false;
        this.message.create("error", response, { nzDuration: 3000 });
      });
  }
  //删除
  del(id) {
    let url = "api/City/Delete?Id=" + id;
    this.http.delete(url).subscribe(
      (val) => {
        this.GetCityList();
        this.message.create("success", val.toString(), { nzDuration: 3000 });
      },
      response => {
        this.message.create("error", response, { nzDuration: 3000 });
      });
  }

}
