  updateAllChecked_Device(): void {
    this.indeterminate_Device = false;
    if (this.allChecked_Device) {
      this.DeviceList.forEach(item => item.checked = true);
    } else {
      this.DeviceList.forEach(item => item.checked = false);
    }
  }
  //设施-单选
  updateSingleChecked_Device(): void {
    if (this.DeviceList.every(item => item.checked === false)) {
      this.allChecked_Device = false;
      this.indeterminate_Device = false;
    } else if (this.DeviceList.every(item => item.checked === true)) {
      this.allChecked_Device = true;
      this.indeterminate_Device = false;
    } else {
      this.indeterminate_Device = true;
    }
  }
